.users {
    display: grid; 
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 20px;
}

.user {
    display: grid; 
    background-color: #fff; 
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,.5); 
    transition: .3s linear all;
}

.user:hover {
    box-shadow: 0 1px 10px 0 rgba(0,0,0,.5); 
}

.userPhoto {
    display: block;
    max-width: 100%;
    height: 200px;
    margin: auto;
    border-radius: 50%;
    object-fit: cover; 
}

.userFollow:disabled {
    opacity: .5;
}